html{
  height: 100%;
}

@font-face {
    font-family: Product;
    src :url('./psr.ttf');
}

body{
  font-family: Product;
  background: url('./back.jpeg');
  background-position: center;
  background-size: cover;
}

.App {
  text-align: center;
}

.frm{
    font-family: Bangers;
    width:30%;
    padding:15px;
    margin:20vh auto;
    background:rgba(255, 255, 255, 0.5);
    border:5px solid #000;
}

h3{
  text-align: center;
  font-size: 48px;
  font-family: Bangers;
}

h2{
  text-align: center;
}

.btn{
  font-family: Bangers;
  letter-spacing: 0.5px;
}

.error{
  margin: 10px auto 0;
  text-align: center;
}

.link{
  display: block;
  margin: 2px auto;
  text-align: center;
  text-decoration: none !important;
  color:#555;
}

.form-group{
    padding-left:10px;
    padding-right: 10px;
    padding-top: 15px;
    margin: 5px;
}

.form-control{
    padding: 15px;
    margin: 10px auto;
    background:rgba(255, 255, 255, 0.7);
    border:1px solid grey;
    border-radius:0;
}

.btn-primary,.btn-danger{
    background-color:rgba(0, 0, 0, 0.8);
    position:relative;
    display:block;
    -webkit-box-shadow:0 4px rgba(0, 0, 0, 0.9);
            box-shadow:0 4px rgba(0, 0, 0, 0.9);
    border:0;
    outline:none;
}

.btn-danger{
  position: absolute;
  top:15px;
  right:5px;
}
.btn-danger:hover,.btn-danger:active,.btn-danger:focus{
  background-color: rgba(0, 0, 0, 0.9);
  -webkit-box-shadow:0 2px rgba(0, 0, 0, 1);
          box-shadow:0 2px rgba(0, 0, 0, 1);
  border:0;
}

.btn-primary:hover{
    background-color: rgba(0, 0, 0, 0.9);
    -webkit-box-shadow:0 2px rgba(0, 0, 0, 1);
            box-shadow:0 2px rgba(0, 0, 0, 1);
    border:0;
    top:2px;
}

.btn-primary:active{
    background-color: rgba(0, 0, 0, 0.9) !important;
    outline:none !important;
    -webkit-box-shadow:0 0 rgba(0, 0, 0, 1);
            box-shadow:0 0 rgba(0, 0, 0, 1);
    border:0;
    top:4px;
}

.btn-primary:focus{
    background-color: rgba(0, 0, 0, 0.9) !important;
    outline:none !important;
    -webkit-box-shadow:0 0 rgba(0, 0, 0, 1);
            box-shadow:0 0 rgba(0, 0, 0, 1);
    border:0;
    top:4px;
}

.goals{
  display: -ms-inline-flexbox;
  display: inline-flex;
  padding: 5px;
  margin: 5px;
  /* flex-direction: column; */
  -ms-flex-align: center;
      align-items: center;
}

.btn-sm{
  margin-top: 0;
}

.clear{
  margin: 0 auto;
}

strong{
  font-size: 18px;
}

.btn-success{
  background-color: #039be5;
}

@media screen and (max-width:767px){
  .btn-danger{
    top:20px;
  }
  body{
    background-position: right;
    font-size: 16px;
  }
  h3{
    font-size: 32px;
  }
  .btn-sm{
    margin-top: 0px;
  }
  .goals{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 10px;
  }
  .frm{
    width:90%;
    padding:15px;
    margin:10vh auto;
    background:rgba(255, 255, 255, 0.5);
    border:5px solid #000;
    }

    .form-group{
        padding-left:10px;
        padding-right: 10px;
    }

    .form-control{
        background:rgba(255, 255, 255, 0.7);
        border:1px solid grey;
        border-radius:0;
    }
  }



